import { LocalStorage } from "helpers/webStorage";

export default class PageCache {
  keyPrefix: string;
  cachedKeys: string[] = [];

  constructor(pathname: string) {
    this.keyPrefix = pathname;
  }

  private getPageKey(key: string) {
    return `${this.keyPrefix}-${key}`;
  }

  public set = (key: string, data: any = null): void => {
    const pageKey = this.getPageKey(key);
    LocalStorage.setItem(pageKey, JSON.stringify(data));
    this.cachedKeys.push(key);
  };

  public get = <Result>(key: string): Result | null => {
    const pageKey = this.getPageKey(key);
    return JSON.parse(LocalStorage.getItem(pageKey) ?? "null");
  };

  public remove = (key: string): void => {
    const pageKey = this.getPageKey(key);
    LocalStorage.removeItem(pageKey);
    this.cachedKeys = this.cachedKeys.filter((k) => k !== key);
  };

  public clearAll = () => {
    this.cachedKeys.forEach((key) => this.remove(key));
    this.cachedKeys = [];
  };
}
