import React, { useMemo, useRef } from "react";
import { useLocation } from "react-router-dom";

import PageCache from "helpers/pageCache";
import {
  sanitizeNumericInput,
  getDefaultFavorites,
  calcValue,
  calcValueForFavorites
} from "models/tools/unitConverter";
import {
  E_PageCacheKeys,
  E_UnitConverterActions,
  E_UnitConverterListItemActions,
  T_FavoriteCodes,
  T_UnitConverterAction,
  T_UnitConverterConversionLogicProps,
  T_UnitConverterPropsDefaults,
  T_UnitConverterState
} from "./types";

export function useElementHandlers(
  dispatch: React.Dispatch<T_UnitConverterAction>,
  pageCache: PageCache
) {
  const inputValueEl = useRef<HTMLInputElement>(null);
  const targetValueEl = useRef<HTMLInputElement>(null);

  const eventHandlers = useMemo(() => {
    const handleInputValueChange = (
      event: React.ChangeEvent<HTMLInputElement>
    ) => dispatch([E_UnitConverterActions.setInputValue, event.target.value]);
    const handleTargetValueChange = (
      event: React.ChangeEvent<HTMLInputElement>
    ) => dispatch([E_UnitConverterActions.setTargetValue, event.target.value]);
    const handleListItemAction = (
      event: React.MouseEvent<HTMLButtonElement, MouseEvent>
    ) => {
      const { code, actionName } = event.currentTarget.dataset;
      if (code && actionName) {
        switch (actionName as unknown as E_UnitConverterListItemActions) {
          case E_UnitConverterListItemActions.input: {
            pageCache.set(E_PageCacheKeys.inputCode, code);
            dispatch([E_UnitConverterActions.setInputCode, code]);
            inputValueEl.current?.focus();
            return;
          }
          case E_UnitConverterListItemActions.target: {
            pageCache.set(E_PageCacheKeys.targetCode, code);
            dispatch([E_UnitConverterActions.setTargetCode, code]);
            targetValueEl.current?.focus();
            return;
          }
          default:
            return;
        }
      }
    };
    const handleInputTargetCodeSwapClick = (
      inputCode: string,
      targetCode: string
    ) => {
      pageCache.set(E_PageCacheKeys.inputCode, targetCode);
      pageCache.set(E_PageCacheKeys.targetCode, inputCode);
      dispatch([E_UnitConverterActions.swapInputTargetCode]);
      inputValueEl.current?.focus();
    };
    const handleInputClearClick = () => {
      dispatch([E_UnitConverterActions.setInputValue, ""]);
      inputValueEl.current?.focus();
    };
    const handleTargetClearClick = () => {
      dispatch([E_UnitConverterActions.setTargetValue, ""]);
      targetValueEl.current?.focus();
    };

    return {
      handleInputValueChange,
      handleTargetValueChange,
      handleListItemAction,
      handleInputClearClick,
      handleTargetClearClick,
      handleInputTargetCodeSwapClick
    };
  }, [dispatch, pageCache]);

  return {
    inputValueEl,
    targetValueEl,
    ...eventHandlers
  };
}

export function useDefaults(defaults: T_UnitConverterPropsDefaults) {
  const location = useLocation();

  return useMemo(() => {
    const pageCache = new PageCache(location.pathname);
    return {
      inputCode:
        pageCache.get<string>(E_PageCacheKeys.inputCode) || defaults.inputCode,
      targetCode:
        pageCache.get<string>(E_PageCacheKeys.targetCode) ||
        defaults.targetCode,
      favorites: getDefaultFavorites(
        (pageCache.get(E_PageCacheKeys.favoriteCodes) as T_FavoriteCodes) ||
          defaults.favoriteCodes
      ),
      pageCache
    };
  }, [location, defaults]);
}

export function createReducer(props: T_UnitConverterConversionLogicProps) {
  return (
    state: T_UnitConverterState,
    [type, payload]: T_UnitConverterAction
  ) => {
    if (!props.conversionRates && !props.conversionFormulas) {
      return state;
    }

    const conversionLogicProps = {
      conversionRates: props.conversionRates,
      conversionFormulas: props.conversionFormulas
    };

    switch (type) {
      case E_UnitConverterActions.setInputCode: {
        return {
          ...state,
          inputCode: payload,
          targetValue: calcValue({
            ...conversionLogicProps,
            inputCode: payload,
            targetCode: state.targetCode,
            inputValue: state.inputValue
          }),
          favorites: calcValueForFavorites({
            ...conversionLogicProps,
            favorites: state.favorites,
            inputCode: payload,
            inputValue: state.inputValue
          })
        };
      }

      case E_UnitConverterActions.setTargetCode: {
        const newInputValue = calcValue({
          ...conversionLogicProps,
          inputCode: payload,
          targetCode: state.inputCode,
          inputValue: state.targetValue
        });

        return {
          ...state,
          targetCode: payload,
          inputValue: newInputValue,
          favorites: calcValueForFavorites({
            ...conversionLogicProps,
            favorites: state.favorites,
            inputCode: state.inputCode,
            inputValue: newInputValue
          })
        };
      }

      case E_UnitConverterActions.swapInputTargetCode: {
        return {
          ...state,
          inputCode: state.targetCode,
          targetCode: state.inputCode,
          inputValue: state.targetValue,
          targetValue: state.inputValue
        };
      }

      case E_UnitConverterActions.setInputValue: {
        const sanitizedInputValue = sanitizeNumericInput(payload);

        // Avoid state update (and re-render) if there is no change
        if (sanitizedInputValue === state.inputValue) {
          return state;
        }

        return {
          ...state,
          inputValue: sanitizedInputValue,
          targetValue: calcValue({
            ...conversionLogicProps,
            inputCode: state.inputCode,
            targetCode: state.targetCode,
            inputValue: sanitizedInputValue
          }),
          favorites: calcValueForFavorites({
            ...conversionLogicProps,
            favorites: state.favorites,
            inputCode: state.inputCode,
            inputValue: sanitizedInputValue
          })
        };
      }

      case E_UnitConverterActions.setTargetValue: {
        const sanitizedTargetValue = sanitizeNumericInput(payload);

        // Avoid state update (and re-render) if there is no change
        if (sanitizedTargetValue === state.targetValue) {
          return state;
        }

        const newInputValue = calcValue({
          ...conversionLogicProps,
          inputCode: state.targetCode,
          targetCode: state.inputCode,
          inputValue: sanitizedTargetValue
        });

        return {
          ...state,
          inputValue: newInputValue,
          targetValue: sanitizedTargetValue,
          favorites: calcValueForFavorites({
            ...conversionLogicProps,
            favorites: state.favorites,
            inputCode: state.inputCode,
            inputValue: newInputValue
          })
        };
      }

      case E_UnitConverterActions.toggleFavorite: {
        const favoritesArrWithoutPayloadItem = state.favorites.filter(
          (favItem) => favItem.code !== payload
        );

        // Check if the filtering resulted in a smaller array than state.favorites
        if (favoritesArrWithoutPayloadItem.length < state.favorites.length) {
          // This means that the supplied item already existed in state.favorites
          // and we just obtained an array after removing it.
          return {
            ...state,
            favorites: favoritesArrWithoutPayloadItem
          };
        }

        // Otherwise it means that payload item is a new item to be favorited
        return {
          ...state,
          favorites: [...state.favorites, payload]
        };
      }
      default:
        return state;
    }
  };
}
