import React from "react";
import {
  T_ConversionFormulas,
  T_UnitConversionAllUnitsMap
} from "types/tools/unitConverter";
import { T_Object } from "types/generic";
import { T_StringOrNumber } from "types/common";

export const enum E_PageCacheKeys {
  inputCode = "inputCode",
  targetCode = "targetCode",
  favoriteCodes = "favoriteCodes"
}

export const enum E_UnitConverterListItemActions {
  input = "INPUT",
  target = "TARGET"
}

export const enum E_UnitConverterActions {
  setInputCode,
  setTargetCode,
  setInputValue,
  setTargetValue,
  swapInputTargetCode,
  toggleFavorite
}

export type T_ConverionRateAPIFn = (...args: any[]) => Promise<
  | {
      conversionRates: T_ConversionRates;
    }
  | undefined
  | null
>;

export type T_UnitConverterPropsDefaults = {
  inputCode: string;
  targetCode: string;
  favoriteCodes: T_FavoriteCodes;
};

export type T_UnitConverterConversionLogicProps = {
  conversionRates?: T_ConversionRates;
  conversionFormulas?: T_ConversionFormulas;
};

export type T_UnitConverterProps = {
  allUnitsMap: T_UnitConversionAllUnitsMap;
  defaults: T_UnitConverterPropsDefaults;
  UnitIconComponent?: React.ComponentType<{
    code: string;
    wrapperClassName?: string;
    iconClassName?: string;
  }>;
} & T_UnitConverterConversionLogicProps;

export type T_FavoriteItemWithValue = {
  code: string;
  value: T_StringOrNumber;
};

export type T_FavoriteCodes = string[];

export type T_FavoritesWithValue = T_FavoriteItemWithValue[];

export type T_ConversionRates = T_Object<number>;

export type T_UnitConverterAction = [
  type: E_UnitConverterActions,
  payload?: any
];

export type T_UnitConverterState = {
  inputCode: string;
  targetCode: string;
  favorites: T_FavoritesWithValue;
  inputValue: string | number;
  targetValue: string | number;
};
