export function isStorageSupported(type: string): boolean {
  try {
    const storage = (window as { [key: string]: any })[type];
    const testKey = "__storage_test__";

    storage.setItem(testKey, "1");
    storage.removeItem(testKey);

    return true;
  } catch (error) {
    return false;
  }
}

// Wrapper class for storage
export class MemoryStorage {
  public storage: { [x: string]: string } = {};

  public setItem = (key: string, data: string): void => {
    const json = (data && data.toString()) || "";

    this.storage[key] = json;
  };

  public getItem = (key: string): string | null => this.storage[key] || "";

  public removeItem = (key: string): void => {
    delete this.storage[key];
  };

  public clear = () => {
    this.storage = new MemoryStorage().storage;
  };
}

export const LocalStorage = isStorageSupported("localStorage")
  ? window.localStorage
  : new MemoryStorage();

export const SessionStorage = isStorageSupported("sessionStorage")
  ? window.sessionStorage
  : new MemoryStorage();
